.carousel {
	width: 100%;
	height: 700px;
	background-color: black;
}

@media screen and (max-width: 600px) {
	.carousel {
		width: 100%;
		height: 350px;
		background-color: black;
	}
}

.carouselInner {
	height: 100%;
	width: 100%;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	display: flex;
}

.carouselInner .left {
	flex: 5%;
	height: 100%;
	background-color: rgb(0, 0, 0, 0.6);
	display: grid;
	place-items: center;
	color: white;
	cursor: pointer;
}

.carouselInner .center {
	flex: 80%;
	height: 100%;
	display: grid;
	place-items: center;
	font-family: Arial, Helvetica, sans-serif;
	text-align: justify;
	text-align-last: center;
}

.carouselInner .right {
	flex: 5%;
	height: 100%;
	background-color: rgb(0, 0, 0, 0.6);
	display: grid;
	place-items: center;
	color: white;
	cursor: pointer;
}
